<template>
    <div> Loading... </div>
</template>
<script>
    export default {
        name: 'app',
        async created() {
            var token = await process.env.NODE_ENV === "development" ? this.$root.user.access_token :this.$store.state.clientAppToken; //this.$root.access_token;
            await this.$store.commit("updateSession",token);
            switch (this.$store.getters.clientAppDecodedToken.Role) {
                case "TsspAdmin":
                case "TsspIntermediate":
                case "TaxAuthRevenueOfficer":
                case "TaxAuthAdmin":
                     this.$router.push("MfListing")
                    break;  
                case "TpsafAdmin":
                case "TpsafFacilityIncharge":
                case "TpsafFacilityAdmin":
                    this.$router.push("InternalStockRequest")    
                    break;         
                case "MfAdmin":
                case "MfAccountManager":
                case "MfWarehouseIncharge": {
                    await this.$store.dispatch("getManufacturerDtoByIdAsync", this.$store.getters.clientAppDecodedToken.OrgId);
                    if (this.$store.getters.manufacturerDto.status===1)
                        this.$router.push({ name: 'MfActive', params: { mfId: this.$store.getters.manufacturerDto.mfId }});
                    else{
                        var lastApproval=this.$store.getters.manufacturerDto.approvals[this.$store.getters.manufacturerDto.approvals.length-1];
                        if(lastApproval.action===3)
                            this.$router.push({ name: 'MfRejected',params: { mfId: this.$store.getters.manufacturerDto.mfId }});
                        else if(lastApproval.action===1 || lastApproval.action===4)
                            this.$router.push({ name: 'ViewMf',params: { mfId: this.$store.getters.manufacturerDto.mfId }});
                        else 
                            this.$router.push({ name: 'MfInActive',params: { mfId: this.$store.getters.manufacturerDto.mfId }});
                    }  
                    break;
                }
            }
        }
    }
</script>